
html, body
{
    margin: 0px; padding: 0px;
    max-height: 100%;
    overflow-x: hidden;
}



.clickable{
color: black !important;
}

.clickable:hover{
  color: #ffc3b3 !important;
    cursor: pointer;
}

.quantity__clickable{
  color: black !important;
 }
 
 .quantity__clickable:hover{
    color: #ffc3b3 !important;
    cursor: pointer;
 }

@media only screen and (max-width: 767px){
    .sidebar{
        height: 100vh;
        width: 100vw;
        top: -14px;
        z-index: 1;
        position: relative;
     }
     .sidebarbig{
      display: none;
    }
    .cart__quantitycolor{
      color: white;
      -webkit-text-stroke: 1px black; 
    }
  }

  @media only screen and (min-width: 768px){
    .sidebar{
        height: calc(100vh - 3em);
        width: 400px;
        float: right;
        margin-top: -14px;
        margin-right: -400px;
        display: none;
     }
     .sidebarsmall{
      display: none;
     }

     .responsive__sticky{
        position: sticky !important;
        top: 0;
        background-color: white; 
        z-index: 10;
     }

  }



  .ui.search>.results .result .image {
    height: 5em !important;
  }


.red .price{
    color: red !important;
}

.ui.search>.results .result .description {
    overflow: hidden;
   text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }

  .limittext{
    overflow: hidden;
    text-overflow: ellipsis;
     display: -webkit-box;
     -webkit-line-clamp: 3;
     -webkit-box-orient: vertical;
  }

  .removecart:hover{
      cursor: pointer;
  }

  .slide-enter {
    margin-right: -400px !important;
    display: inline !important;
  }
  .slide-enter-done {
    margin-right: 0px !important;
    display: inline !important;
  }
  .slide-enter-active {
    margin-right: 0px !important;
    transition: all 400ms;
    -webkit-transform: translateZ(0);
-moz-transform: translateZ(0);
-ms-transform: translateZ(0);
-o-transform: translateZ(0);
transform: translateZ(0);
    display: inline !important;
  }
  .slide-exit {
    margin-right: 0px !important;
    display: inline !important;
  }
  .slide-exit-active {
    margin-right: -400px !important;
    transition: all 400ms;
    -webkit-transform: translateZ(0);
-moz-transform: translateZ(0);
-ms-transform: translateZ(0);
-o-transform: translateZ(0);
transform: translateZ(0);
    display: inline !important;
  }
  .slide-exit-done {
    margin-right: -400px !important;
  }


 