@media only screen and (max-width: 767px){
    .responsive__quickshopsmall{
        visibility: visible ;
     }
    .responsive__quickshopbig{
        visibility: hidden !important;
     }
     
  }

  @media only screen and (min-width: 768px){
    .responsive__quickshopsmall{
        visibility: hidden;
     }
    .responsive__quickshopbig{
        visibility: visible;
     }
     
    
  }
  @media only screen and (max-width: 900px){
    .responsive__categoriesbig{
        display: none !important;
    }
    .banner__button{
        visibility: hidden;
    }
  }

  @media only screen and (min-width: 901px){
    .responsive__categoriessmall{
        display: none !important;
    }
    .category__image:hover{
        cursor: pointer;
        transform: scale(1.5);
    }
    #categorybutton__zoomimage__environment:hover ~ #sibling__environment #categoryimage__environment{
        transform: scale(1.5);
    }
    #categorybutton__zoomimage__animals:hover ~ #sibling__middle #categoryimage__animals{
        transform: scale(1.5);
    }
    #categorybutton__zoomimage__aesthetic:hover ~ #sibling__middle #categoryimage__aesthetic{
        transform: scale(1.5);
    }
    #categorybutton__zoomimage__buildings:hover ~ #sibling__buildings #categoryimage__buildings{
        transform: scale(1.5);
    }
  }
  @media only screen and (max-width: 1070px){
    .responsive__item{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .responsive__otherimages{
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
    }
    .responsive__item__description{
        max-width: 75%;
    }
    .responsive__item__description{
        margin-left: 25px;
        margin-right: 25px;
    }
    .banner__header{
        font-size: 2em;
    }
    .banner__subheader{
        font-size: 1em;
    }
    .img-zoom-result {
         display: none !important;
    }
    .img-zoom-lens {
        display: none !important;
    }
  }

  @media only screen and (min-width: 1071px){

    .responsive__item{
        display: flex;
    }
    .responsive__item__description{
        margin-left: 25px;
        margin-right: 25px;
        width: 40%;
        max-width: 450px;
    }
    .banner__header{
        font-size: 4em;
    }
    .banner__subheader{
        font-size: 2em;
    }
  }


.banner__dot{
    background-color: rgb(155, 155, 155);
    width: 19px;
    height: 19px;
    border-radius: 50%;
    margin: 0 5px;
    z-index: 7;
}

.banner__dot:hover{
    background-color: rgb(82, 82, 82);
    cursor: pointer;
    z-index: 7;
}

.banner__currentdot{
    background-color: rgb(32, 32, 32);
    width: 19px;
    height: 19px;
    border-radius: 50%;
    margin: 0 5px;
    z-index: 7;
}

.card{
    margin-left: 20px !important;
    margin-right: 20px !important;
    box-shadow: none !important;
}

.myvisibile{
    visibility: visible;
}

.myhidden{
    visibility: hidden;
}

.option__value:hover{
    background-color: #ffe1d9;
    cursor: pointer;
}

.option__value__enabled{
    background-color: #ffc3b3;
}

.other__images:hover{
    cursor: pointer;
    transform: translate(0px, -2px);
}

.other__images{
    margin-bottom: 7px;
    margin-right: 3px;
    opacity: 0.5;
}

.categories__label{
    min-width: 50%;
    background-color: #ffc3b3 !important;
    color: black !important;
    border-radius: 0 !important;
    border: solid 1px black !important;
    z-index: 6;
}
.categories__label:hover{
background-color: #ffa28a !important;
}

.category__image:hover{
    cursor: pointer;
}

.category__image{
    transition: transform 1s;
}

.banner__texthover:hover{
cursor: pointer;
}


.img-zoom-container {
    position: relative;
  }
  
  .img-zoom-lens {
    position: absolute;
    display: none;
    border: 1px solid #d4d4d4;
    /*set the size of the lens:*/
    width: 100px;
    height: 100px;
  }
  
  .img-zoom-result {
    position: absolute;
    display: none;
    border: 1px solid #d4d4d4;
    /*set the size of the result div:*/
    width: 450px;
    height: 450px;
    z-index: 50;
  }
